<template>
  <div class="cart-body">
    <!-- 无数据时展示 -->
    <div v-if="cartTotal === 0" class="cart-warning">购物车空空如也~快去选购吧！</div>
    <!-- 数据展示 -->
    <div v-if="Object.keys(cartItem).length !== 0">
      <!-- 循环展示店铺名称 -->
      <div v-for="(value, name) in cartItem" :key="name" class="cart-shopbox">
        <div class="cart-shopname">
          <span>{{ value[0].shop }}</span>
        </div>
        <!-- 循环展示商品 -->
        <div v-for="item in value" :key="item.iid" class="cart-goodsbox">
          <check-box
            class="cart-goods-checkbox"
            :isActived="item.isActived"
            @click.native="changeGoodsActived(name, item.iid)"
          ></check-box>
          <div class="cart-imgbox">
            <div class="cart-imgbox-cut" @click="goDetail(item.iid)">
              <img :src="item.image[0]" alt="" />
            </div>
          </div>
          <div class="cart-goodsmsg">
            <p class="cart-goodstitle" @click="goDetail(item.iid)">{{ item.title }}</p>
            <p class="cart-goodsprice">
              <span class="cart-goods-nowprice"><i>￥</i>{{ item.price }}</span>
              <span v-if="item.oldPrice" class="cart-goods-oldprice">{{ item.oldPrice }}</span>
            </p>
            <div class="count">×{{ item.quan }}</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import CheckBox from '@/components/content/checkBox'

import { mapState, mapGetters, mapMutations } from 'vuex'

export default {
  name: 'CartBody',
  components: {
    CheckBox
  },
  computed: {
    ...mapState(['cartItem']),
    ...mapGetters(['cartTotal'])
  },
  methods: {
    ...mapMutations({ vuexChangeGoodsActived: 'changeGoodsActived' }),
    goDetail(iid) {
      this.$router.push('/detail/' + iid)
    },
    changeGoodsActived(shopid, iid) {
      this.vuexChangeGoodsActived({ shopid, iid })
    }
  }
}
</script>

<style lang='less' scoped>
.cart-body {
  margin-bottom: 50px;
  overflow: hidden;
  color: #000;
  .cart-warning {
    font-size: 20px;
    text-align: center;
    margin: 30px auto;
  }

  .cart-shopbox {
    margin-top: 20px;
    margin-left: 5%;
    margin-right: 5%;
    border: 1px solid #efefef;
    border-radius: 10px;
    padding-left: 5%;
    padding-right: 5%;
    padding-bottom: 20px;
    .cart-shopname {
      line-height: 40px;
      font-size: 16px;
      font-weight: 550;
    }
    .cart-goodsbox {
      display: flex;
      margin-top: 10px;
      overflow: hidden;
      .cart-goods-checkbox {
        margin-top: 60px;
        transform: translate(0, -50%);
      }
      .cart-imgbox {
        width: 30%;
        height: 120px;
        overflow: hidden;
        border-radius: 5px;
        text-align: center;
        .cart-imgbox-cut {
          width: 80px;
          height: 100%;
          margin: 0 auto;
          overflow: hidden;
          border-radius: 5px;
          img {
            height: 100%;
            width: auto;
          }
        }
      }
      .cart-goodsmsg {
        position: relative;
        display: block;
        width: 60%;
        height: 120px;
        .cart-goodstitle {
          position: absolute;
          top: 0;
          left: 5%;
          font-size: 14px;
          text-overflow: -o-ellipsis-lastline;
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
        }
        .cart-goodsprice {
          position: absolute;
          bottom: 0;
          left: 5%;
          .cart-goods-nowprice {
            font-size: 18px;
            font-weight: 700;
            color: var(--color-high-text);
            i {
              font-style: normal;
              font-size: 12px;
            }
          }
          .cart-goods-oldprice {
            margin: 0 10px;
            text-decoration: line-through;
            color: #aaa;
            font-size: 14px;
          }
        }
        .count {
          position: absolute;
          right: 0;
          bottom: 0;
          font-weight: 700;
          font-size: 18px;
        }
      }
    }
  }
}
</style>
