<template>
  <div class="tabbar-item" @click="changeRoute()">
    <img :src="trueSrc" class="tabbar-item-img" />
    <p class="tabbar-item-title" :style="fontColor">{{ title }}</p>
  </div>
</template>

<script>
export default {
  name: 'TabBarItem',
  props: {
    // 文字
    title: String,
    // 一般图片路径
    imgSrc: String,
    // 选中状态图片路径
    imgSrcCurrent: String,
    // 导航路由地址 添加必传标识让报错信息更加友好
    path: {
      type: String,
      required: true
    }
  },
  computed: {
    trueSrc() {
      return this.isCurrent ? this.imgSrcCurrent : this.imgSrc
    },
    fontColor() {
      // 如果当前为选中状态不改变文字颜色
      if (!this.isCurrent) {
        return { color: '#000' }
      }
      // 否则将将文字颜色设为红色或者用户在父组件指定的选中颜色
      return this.$parent.$props.currentItemColor
        ? { color: this.$parent.$props.currentItemColor }
        : { color: '#f00' }
    },
    // 判断当前item是否为选中状态
    isCurrent() {
      return this.$route.path.indexOf(this.path) !== -1
    }
  },
  methods: {
    // 被点击后将路由地址改为传入的路由地址
    changeRoute() {
      if (this.$route.path.indexOf(this.path) === -1) {
        this.$router.push(this.path)
      }
    }
  }
}
</script>

<style lang='less' scoped>
.tabbar-item {
  flex: 1;
  text-align: center;
}

.tabbar-item-img {
  margin-top: 5px;
  width: 25px;
  height: 25px;
  vertical-align: bottom;
}

.tabbar-item-title {
  font-size: 12px;
  line-height: 16px;
}
</style>
