<template>
  <div class="cart-footbar">
    <div class="left cart-footbar-left" @click="toAllSelect">
      <check-box class="cart-footbar-check-box" :isActived="isAllSelected"></check-box>
      <span class="cart-footbar-text">全选({{ activeCount }})</span>
    </div>
    <div class="right cart-footbar-right">
      <span class="cart-footbar-ttprice"><i>￥</i>{{ totalPrice | priseFilter }}</span>
      <div class="cart-footbar-settle" @click="$toast('功能未实现')">结算</div>
    </div>
  </div>
</template>

<script>
import CheckBox from '@/components/content/checkBox'

import { mapGetters, mapActions } from 'vuex'

export default {
  name: 'CartFootbar',
  components: {
    CheckBox
  },
  computed: {
    ...mapGetters(['isAllSelected', 'activeCount', 'totalPrice'])
  },
  filters: {
    priseFilter(value) {
      const qieGe = value.toString().split('.')
      // console.log(qieGe, qieGe[1].substring(0, 2))
      if (qieGe.length === 1) {
        return value + '.00'
      } else if (qieGe[1].length < 2) {
        return value + '0'
      } else if (qieGe[1].length > 2) {
        return qieGe[0] + '.' + qieGe[1].substring(0, 2)
      }
      return value
    }
  },
  methods: {
    ...mapActions(['toAllSelect'])
  }
}
</script>

<style lang='less' scoped>
.cart-footbar {
  position: fixed;
  padding: 0 20px;
  bottom: 49px;
  left: 0;
  right: 0;
  height: 40px;
  border-top: 1px solid #eee;
  background-color: #fff;
  .cart-footbar-left {
    position: relative;
    .cart-footbar-check-box {
      position: absolute;
      top: 11px;
      left: 0;
    }
    .cart-footbar-text {
      line-height: 40px;
      margin-left: 25px;
    }
  }
  .cart-footbar-right {
    .cart-footbar-ttprice {
      line-height: 40px;
      margin-right: 10px;
      font-size: 18px;
      font-weight: 700;
      color: var(--color-high-text);
      i {
        font-style: normal;
        font-size: 12px;
      }
    }
    .cart-footbar-settle {
      float: right;
      color: #fff;
      background-color: #ff587c;
      height: 26px;
      width: 60px;
      text-align: center;
      line-height: 26px;
      border-radius: 13px;
      margin-top: 7px;
    }
  }
}
</style>
