<template>
  <div>
    <nav-bar class="cart-nav-bar">
      <template #left>
        <img src="@/assets/img/common/back-w.svg" alt="" @click="returnSuperior" class="back-img" />
      </template>
      <template #default>
        <span class="cart-nav-bar-title">购物车（{{ cartTotal }}）</span>
      </template>
    </nav-bar>
    <cart-body></cart-body>
    <cart-footbar></cart-footbar>
    <main-tab-bar></main-tab-bar>
  </div>
</template>

<script>
import NavBar from '@/components/common/navbar/NavBar.vue'
import MainTabBar from '@/components/content/MainTabBar'

import CartBody from './childrenComponents/CartBody'
import CartFootbar from './childrenComponents/CartFootbar'

import { mapState, mapGetters } from 'vuex'
export default {
  name: 'Cart',
  components: { MainTabBar, NavBar, CartBody, CartFootbar },
  beforeRouteEnter(to, from, next) {
    // 解决切换页面下次进入滚动条不在顶部
    // console.log(from)
    document.documentElement.scrollTop = 0
    next()
  },
  computed: {
    ...mapState(['cartItem']),
    ...mapGetters(['cartTotal'])
  },
  methods: {
    // 返回按钮
    returnSuperior() {
      this.$router.go(-1)
    }
  }
}
</script>

<style lang='less'>
.cart-nav-bar {
  background-color: var(--color-tint);
  .cart-nav-bar-title {
    color: #fff;
    font-weight: 700;
  }
}
.back-img {
  margin-left: 5px;
}
</style>
